.CardCategory{
    margin: 10px 0 0 0;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
}
.CardContainer{
    overflow-x: scroll;
    overflow-y: hidden;
}
.CardContainer::-webkit-scrollbar{
    height: 10px;
    background: rgb(117, 117, 117);
    border-radius: 5px;
}
.CardCategory::-webkit-scrollbar-track{
    -webkit-box-shadow: inset 0 0 5px grey;
            box-shadow: inset 0 0 5px grey;
}
.CardContainer::-webkit-scrollbar-thumb{
    background: #101010;
    border-radius: 5px;
    cursor: pointer!important;
    height: 20px;
}
.box{
    width: 140px;
    height: 180px;
    margin: 0 5px;
    background: red;
}


